import React, { useState, useEffect, Fragment } from 'react';
import { Input } from 'baseui/input';
import { Tag } from 'baseui/tag';
import Eliza from 'elizabot';

const eliza = new Eliza();

const Chatbot = () => {
  const [chats, setChats] = useState([]);
  const [value, setValue] = useState('');
  const [listen, setListen] = useState(false);
  useEffect(() => {
    const setReply = () => {
      const reply = eliza.transform(value);
      chats.push({ id: Date.now(), message: reply });
      setChats([...chats]);
      setListen(false);
    };
    setTimeout(() => {
      if (listen) setReply();
    }, 1000);
  }, [listen]);
  const handleChat = e => {
    console.log(e.key, 'what key');
    if (e.key === 'Enter') {
      console.log('here');
      chats.push({ id: Date.now(), message: value });
      setChats([...chats]);
      setValue('');
      setListen(true);
    }
  };
  console.log(chats, 'what chats');
  return (
    <div>
      <ShowChats chats={chats} />
      <Input
        value={value}
        onChange={e => setValue(e.currentTarget.value)}
        onKeyPress={handleChat}
      />
    </div>
  );
};

const ShowChats = ({ chats }) => {
  console.log(chats, 'chat value received');
  return (
    <Fragment>
      {chats && chats.length
        ? chats.map((chat, index) => (
            <div key={index}>
              <Tag>{chat.message}</Tag>
            </div>
          ))
        : null}
    </Fragment>
  );
};

export default Chatbot;
